<template>
    <div class="user-favorite">
        <div class="com-title-bar">
            <div class="box van-hairline--bottom">
                <div class="back" @click="$routerBack"></div>
                <div class="title">我喜欢的</div>
                <div class="action">
                    <div class="button" @click="editHandel">{{ edit.text }}</div>
                </div>
            </div>
        </div>
        <div class="data" :style="edit.active? 'padding-bottom: 50px':''">

            <div class="empty" v-if="favorite.empty">
                <div class="image">
                    <img src="../../../assets/user/favorite/empty.png">
                </div>
                <div class="text">您还没有喜欢的视频</div>
            </div>

            <div class="box" v-if="!favorite.empty">

                <van-list v-model="favorite.loading" :finished="favorite.finished" finished-text="" @load="initData">
                    <div class="item" v-for="(item,index) in favorite.data" :key="index">
                        <div class="checkbox" v-if="edit.active" @click="selectHandel(index)">
                            <div class="ico" :class="favorite.data[index].checked?'active':''"></div>
                        </div>
                        <div class="video" @click="videoHandel(item.video.uuid)">
                            <div class="image">
                                <img :src="item.video.thumbnail">
                            </div>
                            <div class="info">
                                <div class="title">
                                    {{ item.video.title }}
                                </div>
                                <div class="star">
                                    <div class="ico">
                                        <van-icon name="star" color="#FFEA03"/>
                                    </div>
                                    <div class="text">{{ item.video.star }}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </van-list>

            </div>
        </div>

        <div class="x-action van-hairline--top" v-if="edit.active">
            <div class="box">
                <div class="item select van-hairline--right" @click="allSelectHandel">{{ checkbox.text }}</div>
                <div class="item delete" @click="deleteHandel">删除</div>
            </div>
        </div>

    </div>
</template>

<script>
    import {Icon, List, Checkbox} from 'vant';

    export default {
        components: {
            [Icon.name]: Icon,
            [List.name]: List,
            [Checkbox.name]: Checkbox
        },
        data() {
            return {
                favorite: {
                    data: [],
                    loading: false,
                    prefers: false,
                    empty: false
                },
                page: {
                    favorite: 1
                },
                edit: {
                    active: false,
                    text: '编辑',
                },
                checkbox: {
                    icon: {
                        active: 'http://image.sevend.me/storage/image/common/checkbox/checkbox_active.png',
                        inactive: 'http://image.sevend.me/storage/image/common/checkbox/checkbox.png'
                    },
                    all: false,
                    text: '全选'
                }
            }
        },
        beforeRouteEnter(to, from, next) {
            to.meta.Load = false;
            if (from.path !== '/video/index') {
                to.meta.Load = true;
            }
            next();
        },
        activated() {

            this.edit = {
                active: false,
                text: '编辑',
            };

            this.checkbox = {
                icon: {
                    active: 'http://image.sevend.me/storage/image/common/checkbox/checkbox_active.png',
                    inactive: 'http://image.sevend.me/storage/image/common/checkbox/checkbox.png'
                },
                all: false,
                text: '全选'
            };

            if (this.$route.meta.Load) {
                this.favorite = {
                    data: [],
                    loading: false,
                    prefers: false,
                    empty: false
                };
                this.page = {
                    favorite: 1
                };
            }
        },
        mounted() {

        },
        methods: {
            initData() {
                this.$axios.get('/api/app/video/favorite/store', {
                    page: this.page.favorite
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.page.favorite++;
                            if (this.page.favorite > result.last_page) {
                                this.favorite.finished = true;
                            }
                            for (let i = 0; i < result.data.length; i++) {
                                result.data[i].checked = false;
                            }
                            this.favorite.data = this.favorite.data.concat(result.data);
                            if (this.favorite.data.length === 0) {
                                this.favorite.empty = true;
                            }
                            this.favorite.loading = false;
                        }
                    });
            },
            videoHandel(uuid) {
                this.$router.push('/video/index?uuid=' + uuid)
            },
            editHandel() {
                this.edit.active = !this.edit.active;
                this.edit.text = this.edit.active ? '完成' : '编辑';
            },
            selectHandel(index) {
                this.favorite.data[index].checked = !this.favorite.data[index].checked;
            },
            allSelectHandel() {
                this.checkbox.all = !this.checkbox.all;
                this.checkbox.text = this.checkbox.all ? '取消全选' : '全选';
                for (let i = 0; i < this.favorite.data.length; i++) {
                    this.favorite.data[i].checked = this.checkbox.all;
                }
            },
            deleteHandel() {
                this.$dialog.confirm({
                    title: '提示',
                    message: '确认删除选中的视频？'
                }).then(() => {

                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        mask: false,
                        message: '删除中...'
                    });

                    let video = [];

                    for (let i = 0; i < this.favorite.data.length; i++) {
                        if (this.favorite.data[i].checked) {
                            video.push(this.favorite.data[i].video.uuid);
                        }
                    }

                    this.$axios.post('/api/app/video/favorite/delete', {
                        video: video
                    })
                        .then(response => {
                            let code = response.data.code;
                            if (code === 200) {
                                setTimeout(() => {
                                    this.$toast.success('删除成功');

                                    //状态还原
                                    this.edit = {
                                        active: false,
                                        text: '编辑',
                                    };
                                    this.checkbox = {
                                        icon: {
                                            active: 'http://image.sevend.me/storage/image/common/checkbox/checkbox_active.png',
                                            inactive: 'http://image.sevend.me/storage/image/common/checkbox/checkbox.png'
                                        },
                                        all: false,
                                        text: '全选'
                                    };

                                    //数据重新加载
                                    this.favorite = {
                                        data: [],
                                        loading: false,
                                        prefers: false,
                                        empty: false
                                    };
                                    this.page = {
                                        favorite: 1
                                    };

                                }, 500);
                                return;
                            }
                            this.$toast.fail('请稍后再试~');
                        })

                })
            }
        }
    }
</script>

